.button {
 margin-left: 1%;
 width: 52px;
 padding: 0; }

.div {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin: 0;
  padding: 0; }

.divText {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin: 0;
  padding: 7px; }

.divHome {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin: 10px;
  padding: 0;
  border: 2px solid #5181b8;
  border-radius: 10px; }

.divError {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin: 10px;
  padding: 0;
  border: 2px solid red;
  border-radius: 10px;
  height: 55px; }

.bold {
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin: 0;
  padding: 7px; }


.listNumber {
 width: 20px;
 text-align: center;
 margin-left: 8px;
 padding: 0; }

.listName {
  width: 260px;
  margin: 0;
  padding: 0; }

.listHome {
  width: 260px;
  margin: 0;
  padding: 0; }

.listError {
  width: 260px;
  margin: 0;
  padding: 0;
  color: red;
  font-weight: bold; }



